import wx from "weixin-jsapi";
 
 
import { getJSSDK } from '@/api/wx';//appid,
// import { payorders } from "@/api/appointment";//idappid
 
 
const wxUtils = () => {
  return new Promise((resolve, reject) => {
    getJSSDK().then(async data => {
      await wx.config({
        debug: false, // TODO: 
        appId: data.appid,
        timestamp: data.timestamp,
        nonceStr: data.nonceStr,
        signature: data.signature,
        jsApiList: [
          'getLocation',
          'hideMenuItems'
        ]
      });
      wxReady(resolve)
    }).catch(error => {
      reject();
      console.log(error);
    })
  })
}
// jssdk
const wxReady = resolve => {  //
  wx.ready(() => {
    wx.hideMenuItems({
      menuList: [
        'menuItem:share:timeline', // 
        'menuItem:share:qq', // QQ
        'menuItem:share:weiboApp', // Weibo
        'menuItem:favorite', // 
        'menuItem:share:QZone', //  QQ 
        'menuItem:copyUrl', // 
        'menuItem:openWithQQBrowser', // QQ
        'menuItem:openWithSafari', // Safari
        'menuItem:share:email', // 
        'menuItem:readMode', // 
        'menuItem:originPage' // 
      ] // menu3
    });
    resolve();
  });
};
