import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

import register from '@/components/register.vue'
import publish from '@/components/publish.vue'
import center from '@/components/center.vue'  //
import jyorder from '@/components/jyorder.vue' //
import xcorder from '@/components/xcorder.vue' //
import order_details from '@/components/order_details.vue' //
import xclist from '@/components/xclist.vue' //
import xcdetails from '@/components/xcdetails.vue' //
import home from '@/components/home.vue' //
import jylist from '@/components/jylist.vue' //
import jydetail from '@/components/jydetail.vue' //

Vue.use(Router)

export default new Router({
  routes: [
		{
		  path: '/',
		  name: 'home',
		  component: home
		},
		{
			path: '/register',
			name: 'register',
			component: register
		},
		{
			path: '/publish',
			name: 'publish',
			component: publish
		},
		{
			path: '/center',
			name: '',
			component: center
		},
		{
			path: '/jyorder',
			name: '',
			component: jyorder
		},
		{
			path: '/xcorder',
			name: '',
			component: xcorder
		},
		{
			path: '/order_details',
			name: '',
			component: order_details
		},
		{
			path: '/xclist',
			name: '',
			component: xclist
		},
		{
			path: '/xcdetails',
			name: '',
			component: xcdetails
		},
		{
			path: '/jylist',
			name: 'jylist',
			component: jylist
		},
		{
			path: '/jydetail',
			name: 'jydetail',
			component: jydetail
		},
  ]
})



